@import '/src/variables.scss';


* {
    font-family: "Poppins";
}
.date {
font-size: $mediumFontSize;

};



.myear {
    justify-content: center;
    display: flex;
    flex-direction: row;
    p {
        font-size: $largeFontSize;
        font-weight: $boldFW

    }
};


.month-year{
    margin: 0.5rem;
    font-size: $mediumFontSize;
    font-weight: $bolderFW;
}
.calendarhome {
    display: flex;
    flex-direction: column;
    font-family: $mainFont;
    background-color: $backgroundWhite;
    border-radius: 42px;
    padding-top: 2.5rem;
    margin-left: $marginLeftRight;
    margin-right: $marginLeftRight;
    height: 100%;
    align-items: center;
}




.list-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;   
} ;



.list {
    display: flex;
    font-size: $smallFontSize;
    list-style: none;
    width: 15rem;
    height: 25px;
    padding-bottom: 1rem;
    //background: #D1D0F9;
    border-radius: 1rem;
     p {
        height: 25px;
        width: 240px;
     }
};

.wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-self: center;
    width: 15rem;
}
.schedule {
    width: auto;
    display: flex;
    gap: 0.5rem;
   }

.timeanddescription{
    display: flex;
    flex-direction: row;
    padding: 0px;
    gap: 1rem;
    .color {
        font-size: $smallFontSize;
        color: $whiteFontColor;
        font-weight: $regularFW;
    }
};

.time {
    box-sizing: border-box;
    background: #6563EB;
    width: 20rem;
    border-radius: 8px 8px 0px 0px;
    margin-bottom: 0px;
    color: #ffff
};



.daydescription {
    box-sizing: border-box;
    width: 20rem;
    height: 40rem;
    background: #F1F1F1;
    border: 0.5px solid #D5D5D5;
    border-radius: 0px 0px 8px 8px;
    a {
        text-decoration: none;
        h4{
            font-size: $mediumFontSize;
            font-weight: $boldFW
        }
    }
    
};

@media (max-width: 1100px) {
    .selected-style {
        background-color: #6563EB;
        border-radius: 16px;
        color: $whiteFontColor;
        width: 2rem;
    }
    .wrapper{
        display: none;
    }
    .selected-style-day {
        display: flex;
        margin: auto;
        left: 0;
        right: 0;
    }
    .list {
        align-items: center;
        width: 50px;
        margin-left: 0px;
        padding-top: 10px;
    }

    
}


